.button-neon {
  align-items: center;
  background-image: linear-gradient(144deg, #AF40FF, #5B42F3 50%, #00DDEB);
  border: 0;
  border-radius: 8px;
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  color: #FFFFFF;
  display: flex;
  font-family: 'Phantomsans', sans-serif;
  font-size: 10px;
  justify-content: center;
  line-height: 0.5em;
  padding: 3px;
  cursor: pointer;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  min-width: 140px;
  max-width: 100%;
}
.button-neon:active,
.button-neon:hover {
  outline: 0;
}
.button-neon span {
  background-color: rgb(5, 6, 45);
  padding: 16px 24px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: 300ms;
}
.button-neon:hover span {
  background: none;
}
.button-merge { background-image: linear-gradient(144deg, #bea2a1, #e7810c); }
.button-quick { background-image: linear-gradient(144deg, #04408d, #f7e605); }
.button-bubble { background-image: linear-gradient(144deg, #0841ac, #069778, ); }
.button-insertion { background-image: linear-gradient(144deg, #290101, #8a0416); }
.button-selection { background-image: linear-gradient(144deg, #a00298, #022da3);}
.button-heap { background-image: linear-gradient(144deg, #059711, #d4f806); }
.button-reset { background-image: linear-gradient(144deg, #f8f8f8, rgb(0, 0, 0)); }
@media (min-width: 768px) {
  .button-neon {
    font-size: 24px;
    min-width: 196px;
  }
}
.control-panel {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px;
  gap: 10px;
}
.control-panel label {
  margin: 0 10px 0 0;
}
.control-panel input[type="range"] {
  margin: 0 10px;
}
.control-panel button {
  margin: 0 5px;
}
.button-neon {
  margin: 0;
  flex: 0 0 auto;
}
@media (max-width: 768px) {
  .control-panel {
    flex-direction: column;
  }
  .control-panel label,
  .control-panel input[type="range"],
  .control-panel button {
    margin: 5px 0;
  }
  .button-neon {
    width: 100%;
  }
}
.slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.range {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 15px;
  background: #111;
  outline: none;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 1);
}
.range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #00fd0a;
  cursor: pointer;
  border: 4px solid #333;
}
.slider-description {
  font-size: 1em;
  color: #FFFFFF;
  margin-top: 10px;
  text-align: center;
}
.slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
.slider-value {
  font-size: 1em;
  color: #FFFFFF;
}
#rangeValue {
  font-size: 1em;
  color: #FFFFFF;
}
@media (max-width: 768px) {
  .range {
    width: 90%;
  }
  #rangeValue {
    font-size: 0.8em;
  }
}


.button-neon:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.button-neon:disabled span {
  background-color: #333; 
  color: #888; 
}

.button-neon:disabled:hover span {
  background-color: #333; 
}


.range:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.range:disabled::-webkit-slider-thumb {
  background: #888;  
  cursor: not-allowed;
}


.slider-container.disabled .slider-value,
.slider-container.disabled .slider-description {
  color: #888; 
}